import React from 'react';
import { Chaperone } from 'chaperone';
import { MainContainer, SidePanel, Article, HeadGear } from 'Templates';
import * as Section from './Sections';
import * as Table from './Tables';
// import { Link, Route, SubRouter, useHistory, useLocation } from 'reaouter';

const Reaouter = () => { 
	const title = 'Reaouter (not publish yet)';
	const context = `is a modern react routing library that is delightfully made with hooks. 
	It is so clean to use and easy and understand because it has few imports with few properties in each.
	Even though it is few imports with few properties in each, yet it is powerful and smarter, which 
	can be use in all environment, and even in scaled without head scratcher. one of it's significant feature is 
	the SubRouter, it is use to reduce paths matching in vast environment which can be helpful to your app performances.`;

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`${title} - React Libraries | Mytabworks`}
					description={context.slice(0, 100)}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer 
				title={title}
				context={context}
				main={<ReaouterMain />}
				side={<ReaouterSidePanel />}
			/>
		</>
	);
};

Reaouter.propTypes = {};

