const ReaouterSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Importing' },
		{
			label: 'Getting Started',
			sub: [{ label: 'Controllable Fields' }],
		},
		{
			label: 'API',
			sub: [
				{ label: 'Router' },
				{ label: 'Link' },
				{ label: 'Route' },
				{ label: 'RouteNotFound' },
				{ label: 'SubRouter' },
				{ label: 'Redirect' },
				{
					label: 'Hooks',
					sub: [
						{ label: 'useLocation' },
						{ label: 'useHistory' },
						{ label: 'useParams' }, 
					],
				},
			],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};

export default Reaouter;