export const Importing = `import { 
    Router, 
    Link, 
    Route, 
    SubRouter, 
    RouteNotFound, 
    Redirect,   
    useLocation,  
    useParam,  
    useHistory  
} from "reaouter";`;

export const UseLocation = `const { url, path, location } = useLocation()
return (
    <>
        <Link to={\`\${url}/add\`} label="add">
        <Link to={\`\${url}/edit\`} label="edit">
        <Route path={\`\${path}/add\`} render={<Add />}>
        <Route path={\`\${path}/edit\`} render={<Edit />}>
    </>
)`;

export const UseParams = `/*http://localhost/branch/1/5/add*/
const Branch = () => {
    const { param_one, param_two } = useParams()
    return <div>{\`Do the rest: \${param_one}\`}</div>
}

<Route path="/branch/:param_one/:param_two/add" render={<Branch />} />`;

export const UseHistory = `const { url, redirect, back, forward } = useHistory() 
redirect("/login"),
back()
back(-3)
forward()
forward(2)`;
