import React from 'react';
import { Section, SectionWithCode, Note, Table } from 'Templates';
import * as code from './StringCodes';

export const Installation = () => (
	<SectionWithCode
		readonly
		title='Installation'
		code={`npm i reaouter`}
		language='js'
		theme={{
			plain: {
				color: 'rgb(215,215,215)',
				backgroundColor: 'rgb(86,86,86)',
			},
			styles: [],
		}}
	/>
);

export const Importing = () => (
	<SectionWithCode readonly title='Imports' code={code.Importing} />
);

export const UseLocation = () => (
	<Section
		title='useLocation'
		context={`It is use to get the nested routes url and path to be a reference to its children routes and links.`}>
		<Table.ForAPI
			data={[
				{
					name: 'const {',
					type: '',
					default: '',
					description: '',
				},
				{
					name: 'url,',
					type: 'string',
					default: '-',
					description:
						'It is the sliced URL, based on the path of route you are under with.',
				},
				{
					name: 'path,',
					type: 'string',
					default: '-',
					description: 'It is the path of the route you are under with.',
				},
				{
					name: 'search,',
					type: 'string',
					default: '-',
					description: 'It is use to get url parameters.',
				},
				{
					name: 'hash,',
					type: 'string',
					default: '-',
					description: 'It is use to get url hash.',
				},
				{
					name: 'pathname',
					type: 'string',
					default: '-',
					description: 'It is the current URL which is complete without slice.',
				},
				{
					name: '} = useLocation()',
					type: '',
					default: '',
					description: '',
				},
			]}
		/>
		{/* <SectionWithCode
				readonly
				
				code={code.UseLocation}
			/>  */}
	</Section>
);

export const UseParams = () => (
	<>
		<SectionWithCode
			readonly
			title='useParams'
			context='It is use to get the parameters (you declared in path) in a form of key value pair.'
			code={code.UseParams}
		/>
		<Note
			context='You cannot use it, unless you are under the Route component.'
			style={{ marginTop: '-20px', marginBottom: '20px' }}
		/>
	</>
);

export const UseHistory = () => (
	<Section
		title='useHistory'
		context='It is use to back from previous url, forward to url that once backed, and redirect to another url beside Redirect Component, but its use is outside the JSX.'>
		<Table.ForAPI
			data={[
				{
					name: 'const {',
					type: '',
					default: '',
					description: '',
				},
				{
					name: 'url,',
					type: 'string',
					default: '-',
					description: 'It is current url',
				},
				{
					name: 'back,',
					type: 'function',
					default: '-',
					description: 'It is use to back from previous url',
					data: [
						{
							name: 'function (',
							type: '',
							default: '',
							description: '',
						},
						{
							name: 'index',
							type: 'number',
							default: '-1',
							description: 'It is the index you want to go back.',
						},
						{
							name: ') { return void }',
							type: '',
							default: '',
							description: '',
						},
					],
				},
				{
					name: 'forward,',
					type: 'function',
					default: '-',
					description: 'It is use forward to url that once backed.',
					data: [
						{
							name: 'function (',
							type: '',
							default: '',
							description: '',
						},
						{
							name: 'index',
							type: 'number',
							default: '1',
							description: 'It is the index you want to go forward.',
						},
						{
							name: ') { return void }',
							type: '',
							default: '',
							description: '',
						},
					],
				},
				{
					name: 'push,',
					type: 'function',
					default: '-',
					description:
						'It is use redirect and to push url in the history.',
					data: [
						{
							name: 'function (',
							type: '',
							default: '',
							description: '',
						},
						{
							name: 'to,',
							type: 'string',
							default: '-',
							description: 'It is url where this redirect will lead you.',
						},
						{
							name: 'state',
							type: 'object',
							default: '{}',
							description:
							'It will be attached alongside with the URL on the history and will be remembered even you go back or forward history.',
						},
						{
							name: ') { return void }',
							type: '',
							default: '',
							description: '',
						},
					],
				},
				{
					name: 'replace,',
					type: 'function',
					default: '-',
					description:
						'It is use redirect and to replace the previous url instead to push another in the history.',
					data: [
						{
							name: 'function (',
							type: '',
							default: '',
							description: '',
						},
						{
							name: 'to,',
							type: 'string',
							default: '-',
							description: 'It is url where this redirect will lead you.',
						},
						{
							name: 'state',
							type: 'object',
							default: '{}',
							description:
								'It will be attached alongside with the URL on the history and will be remembered even you go back or forward history.',
						},
						{
							name: ') { return void }',
							type: '',
							default: '',
							description: '',
						},
					],
				},
				{
					name: 'state',
					type: 'object',
					default: '{}',
					description: 'it will get the state that has been attached alongside the URL',
				},
				{
					name: '} = useHistory()',
					type: '',
					default: '',
					description: '',
				},
			]}
		/>
	</Section>
	// <SectionWithCode
	// 	readonly
	// 	code={code.UseHistory}
	// />
);
