import React from 'react';
import { Section, Table } from 'Templates';
const RouterRows = [
	{
		name: 'scrollRestoration',
		type: '"auto" | "manual"',
		default: 'auto',
		description: (
			<>
				The <b>scrollRestoration</b> property of History interface allows web
				applications to explicitly set default scroll restoration behavior on
				history navigation.
				<br />
				<b>auto</b> - The location on the page to which the user has scrolled
				will be restored.
				<br />
				<b>manual</b> - The location on the page is not restored. The user will
				have to scroll to the location manually.
			</>
		),
	},
	{
		name: 'legacy',
		type: 'boolean',
		default: 'false',
		description: 'the hash routing will be applied.',
	},
	{
		name: 'onUpdate',
		type: 'function',
		default: '-',
		description: 'it will be called in every url changes and immidiately in initial load.',
		data: [
			{
				name: 'function({',
				type: '',
				default: '',
				description: '',
			},
			{
				name: 'url',
				type: 'string',
				default: '-',
				description: 'the new or current url.',
			},
			{
				name: 'prevUrl',
				type: 'string',
				default: '-',
				description: 'the old or previous url.',
			},
			{
				name: 'matched',
				type: 'boolean',
				default: '-',
				description: 'it determine if the new url has matched in routes.',
			},
			{
				name: 'initial',
				type: 'boolean',
				default: '-',
				description: 'when true, it means it was called by initial load.',
			},
			{
				name: '}) { return void }',
				type: '',
				default: '',
				description: '',
			},
		]
	},
	{
		name: 'children',
		type: 'element',
		default: '-',
		description: 'A children element to render.',
	},
];

const LinkRows = [
	{
		name: 'as',
		type: 'elementType',
		default: '<a>',
		description: 'You can use a custom element type for this component.',
	},
	{
		name: 'label',
		type: 'string',
		default: '-',
		description: 'The label of the link.',
	},
	{
		name: 'to',
		type: 'string',
		default: '-',
		description:
			'The URL location where this link will lead you to certain path of route.',
	},
	{
		name: 'activable',
		type: 'boolean',
		default: 'true',
		description: (
			<>
				When <b>true</b>, and the <b>to</b> or even related <b>Link</b>{' '}
				<b>to</b> is matched the current URL then, it will be labeled as class{' '}
				<b>active</b>. When <b>false</b>, it will be ignored even it is matched
				the current URL.
			</>
		),
	},
	{
		name: 'nested',
		type: 'boolean',
		default: 'false',
		description:
			'When it is true and activable is true, then it will consider the anchor as an active state eventhough it is not exactly match as long the first few characters in "to" matched the URL.',
	},
	{
		name: 'state',
		type: 'object',
		default: '-',
		description:
			'When clicked, The state will be push alongside with the url in the history and will be remembered even you go back or forward history.',
	},
];

const RouteRows = [
	{
		name: 'render',
		type: 'element',
		default: '-',
		description: 'A children element to render when path is matched.',
	},
	{
		name: 'path',
		type: 'string',
		default: '-',
		description:
			'The route path whenever the current url matched then, the element you put in render property will be shown or rendered.',
	},
	{
		name: 'nested',
		type: 'boolean',
		default: 'false',
		description:
			'When it is true, then the router will know that this is not a real route end point. so when it is matched, it will move on to the decendant routes find the real route end point. but if there has no decendant route that has matched then it is still counted as no matched',
	},
	{
		name: 'exact',
		type: 'boolean',
		default: 'true',
		description:
			'When it is true, then it will only match if the path model is precisely matched to the current url and even the path is just a nested path. It will be matched if the certain part is exactly matched.',
	},
	{
		name: 'immutable',
		type: 'boolean',
		default: 'false',
		description:
			'When it is true, then the page scroll will not move to top to reset. It is use when making a tabs, open a modals or show anything using SubRouter as midiator. as long you put property exact to false so it will allow routes inside it.',
	},
];
const RouteNotFoundRows = [
	{
		name: 'render',
		type: 'element',
		default: '-',
		description:
			'A children element will be rendered immidiately whenever there is no matched in all the routes.',
	},
];

const SubRouterRows = [
	{
		name: 'render',
		type: 'element',
		default: '-',
		description: 'A children element to render when path is matched.',
	},
	{
		name: 'path',
		type: 'string',
		default: '-',
		description:
			'The route path whenever the current url matched then, the element in render property is will be rendered.',
	},
];

const RedirectRows = [
	{
		name: 'path',
		type: 'string',
		default: '-',
		description:
			'The route path when ever the current url matched it. then, it will be redirect to.',
	},
	{
		name: 'to',
		type: 'string',
		default: '-',
		description:
			'The url where this redirect will lead you to certain path when matched.',
	},
	{
		name: 'allowReturnBack',
		type: 'boolean',
		default: 'false',
		description:
			'When true, it will allow you to turn back to the previous url before redirect.',
	},
];

export const Router = () => (
	<Section title='Router' context='It will makes your react pages dynamic.'>
		<Table.ForAPI data={RouterRows} />
	</Section>
);

export const Link = () => (
	<Section title='Link' context='It provides navigation to your application.'>
		<Table.ForAPI data={LinkRows} />
	</Section>
);

export const Route = () => (
	<Section
		title='Route'
		context='It is responsible to render some UI or component when its path matches the current url.'>
		<Table.ForAPI data={RouteRows} />
	</Section>
);

export const RouteNotFound = () => (
	<Section
		title='RouteNotFound'
		context='It is responsible whenever there is no matched on all the routes.'>
		<Table.ForAPI data={RouteNotFoundRows} />
	</Section>
);

export const SubRouter = () => (
	<Section
		title='SubRouter'
		context={
			<>
				It will act as a <b>nested route</b> and a <b>router</b> at the same
				time. It is very effective when the app does have a lot of nested routes
				because whenever you are navigating to its territory, it will not bother the{' '}
				<b>main router</b> to checked all the routes unless the current url is outside its nested domain.
				But if the url doesn't matched, and it is under it's nested domain then,
				you need to handle your own <b>RouteNotFound</b> inside it.
			</>
		}>
		<Table.ForAPI data={SubRouterRows} />
	</Section>
);

export const Redirect = () => (
	<Section title='Redirect'>
		<Table.ForAPI data={RedirectRows} />
	</Section>
);
